.app {
  background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  position: relative;
  z-index: 1;

  .eclipse {
    font-size: 0;
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    z-index: 2;
    background: rgba(0, 0, 0, 0.6);
  }

  .content {
    position: relative;
    z-index: 3;

    .goToBasket {
      cursor: pointer;
      max-width: 140px;
      text-align: center;
      position: fixed;
      bottom: 15px;
      right: -10px;
      font-size: 20px;
      display: flex;
      flex-direction: column-reverse;
      align-items: center;
      transition: 0.1s ease;

      &:hover {
        transform: scale(1.1);
      }

      img {
        width: 40px;
      }
    }
  }
}

