.toolbar {
  display: flex;
  align-items: center;
}

@media (max-width: 991px) {
  .toolbar {
    z-index: 2;
    transition: 0.5s ease;
    padding: 50px 50px;
    font-size: 20px;
    position: fixed;
    top: 0;
    left: 0;
    transform: rotateY(-90deg);
    transform-origin: left;
    background: #000;
    height: 100vh;
    flex-direction: column;
  }

  .toolbarActive {
    transition: 0.5s ease;
    transform: rotateY(0deg);
  }
}

@media (max-width: 480px) {
  .toolbar {
    padding: 50px;
    left: 0;
    right: 0;
    font-size:  25px;
  }
}
