.title {
  text-align: center;
  font-size: 30px;
  padding: 20px 0;
  display: flex;
  justify-content: center;
  align-items: center;

  span {
    background: #fff;
    color: #000;
    border-radius: 50%;
    width: 30px;
    height: 30px;
    margin-right: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.imgWrapper {
  display: flex;
  justify-content: space-around;
  text-align: center;
  font-size: 24px;

  img {
    display: block;
    border-radius: 5px;
    cursor: pointer;
    border: 5px dotted transparent;
    transition: 0.2s linear;
    margin-bottom: 20px;

    &:hover {
      transition: 0.05s linear;
      border: 5px dotted limegreen;
    }
  }

  .active {
    border: 5px solid limegreen;
    transition: 0.1s linear;

    &:hover {
      border: 5px solid limegreen;
    }
  }
}

.submit {
  cursor: pointer;
  font-size: 20px;
  background: green;
  display: inline-block;
  border-radius: 5px;
  transition: 0.1s linear;
  border: 2px solid transparent;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  margin: 50px 0 20px 0;

  button {
    cursor: pointer;
    padding: 15px 30px;
    transition: 0.1s linear;
  }

  &:hover {
    border: 2px solid #D1D1D1;
    transition: 0.1s linear;
    background: darkgreen;
    transform: translateX(-50%) scale(1.1);
  }
}

@media (max-width: 650px) {
  .title {
    font-size: 30px;
    display: flex;
    align-items: flex-start;
    justify-content: center;

    span {
      background: #fff;
      color: #000;
      border-radius: 50%;
      width: 30px;
      height: 30px;
      margin-right: 15px;
      display: flex;
      align-items: center;
      justify-content: center;
    }

    p {
      max-width: 230px;
    }
  }

  .imgWrapper {
    justify-content: space-around;
    font-size: 16px;
    flex-wrap: wrap;

    div {
      width: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      margin-bottom: 10px;
    }

    img {
      display: block;
      margin-bottom: 10px;
    }
  }
}