.headerWrapper {
  background: rgba(0, 0, 0, 0.6);

  .header {
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
    padding: 30px 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    user-select: none;

    .eclipse {
      display: none;
    }

    .burgerBtn {
      display: none;
    }
  }
}

@media (max-width: 991px) {
  .headerWrapper {
    .header {
      justify-content: flex-start;

      .eclipse {
        display: block;
        position: fixed;
        right: 0;
        top: 0;
        width: 100%;
        transform: translateX(100%);
        font-size: 0;
        height: 100vh;
        background: rgba(0, 0, 0, 0.6);
        transition: 0.4s ease;
        z-index: 1;
      }

      .eclipseActive {
        transform: translateX(0);
      }

      .burgerBtn {
        position: fixed;
        right: 15px;
        display: flex;
        flex-direction: column;
        cursor: pointer;
        transition: 0.2s ease;
        z-index: 2;

        &:hover {
          transform: scale(1.1);
        }

        span {
          width: 60px;
          height: 5px;
          background: #fff;
          transition: 0.2s ease;

          &:nth-child(2) {
            margin: 10px 0;
          }
        }
      }

      .burgerBtnActive {
        span {
          &:nth-child(1) {
            transform: translateY(15px) rotate(40deg);
          }

          &:nth-child(3) {
            transform: translateY(-15px) rotate(-40deg);
          }

          &:nth-child(2) {
            opacity: 0;
          }
        }
      }
    }
  }
}

@media (max-width: 480px) {
  .headerWrapper {
    .header {
      padding: 15px;

      .eclipse {
        display: none;
      }

      .burgerBtn {
        span {
          width: 50px;
        }
      }
    }
  }
}



