.deliveryForm {
  margin: 50px auto 0 auto;
  max-width: 500px;

  .title {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 30px;
    margin-bottom: 35px;

    span {
      background: #fff;
      color: #000;
      border-radius: 50%;
      width: 30px;
      height: 30px;
      margin-right: 15px;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }

  div {
    max-width: 600px;
    height: 20px;
    margin: 0 auto;

    input {
      width: 100%;
      color: #fff;
      border: 2px solid #fff;
      background: rgba(0, 0, 0, 0.5);
      padding: 8px 15px;
      border-radius: 3px;
    }
  }

  .btn {
    background: forestgreen;
    cursor: pointer;
    font-size: 20px;
    margin-top: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    max-width: 200px;
    padding: 20px 0;
    border-radius: 5px;
    transition: 0.1s linear;

    &:hover {
      transform: scale(1.1);
    }
  }
}