.productCardWrapper {
  width: 25%;
  display: flex;
  justify-content: center;

  .productCard {
    box-shadow: 0 0 15px #12c2c4;
    position: relative;
    width: 90%;
    background: #1A1A1A;
    border-radius: 3px;
    border: 1px solid rgba(255, 255, 255, 0.3);
    margin-bottom: 30px;
    transition: 0.2s ease;

    &:hover {
      transform: scale(1.05);
      transition: 0.2s ease;
      box-shadow: 0 0 20px lime;
    }

    .basket {
      position: absolute;
      width: 30px;
      top: 52%;
      right: 20px;
      cursor: pointer;
    }

    .dagger {
      display: none;
    }

    .buyZone {
      display: none;
    }

    .bgImg {
      background-size: cover;
      background-position: center center;
      background-repeat: no-repeat;
      height: 200px;
      position: relative;

      .action {
        background: #c61616;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        text-align: center;
        padding: 3px 0;
      }
    }

    .cardContent {
      padding: 15px 15px 20px 15px;

      .name {
        height: 85px;
        font-size: 14px;
        font-family: 'Montserrat', sans-serif;
        font-weight: 500;
        line-height: 17px;
      }

      .stars {
        position: relative;

        .fullStars {
          position: absolute;
          top: 0;
        }
      }

      .comment {
        color: #7072FF;
        margin: 10px 0;
        cursor: pointer;
        display: inline-block;

        &:hover {
          text-decoration: underline;
        }
      }

      .inStock {
        font-size: 16px;
        color: #17B978;
        margin-bottom: 10px;
      }

      .havNot {
        font-size: 16px;
        color: #c61616;
        margin-bottom: 10px;
      }

      .producer {
        margin-bottom: 10px;
      }

      .id {
        margin-bottom: 10px;
      }

      .moreDetail {
        color: blueviolet;
        font-size: 20px;
      }

      .price {
        margin-top: 40px;
        margin-bottom: 10px;
        text-align: center;
        font-size: 30px;
        color: #296B4D;

        span {
          font-size: 16px;
          color: #D1D1D1;
        }
      }

      .buttonWrapper {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        .buy {
          background: green;
          display: inline-block;
          cursor: pointer;
          border-radius: 5px;
          padding: 8px 15px;
          font-size: 20px;
          margin-bottom: 15px;
          transition: 0.1s linear;

          &:hover {
            transition: 0.1s linear;
            transform: scale(1.1);
          }
        }

        .disabledBuy {
          background: grey;
          display: inline-block;
          border-radius: 5px;
          cursor: default;
          padding: 8px 15px;
          font-size: 20px;
          margin-bottom: 15px;
        }

        .inBasket {
          cursor: pointer;
          background: #196bbf;
          border-radius: 5px;
          padding: 8px 15px;
          font-size: 20px;
          display: inline-block;
          transition: 0.1s linear;

          &:hover {
            transition: 0.1s linear;
            transform: scale(1.1);
          }
        }

        .disabledBasket {
          cursor: default;
          background: grey;
          border-radius: 5px;
          padding: 8px 15px;
          font-size: 20px;
          display: inline-block;
        }

        .outBasket {
          cursor: pointer;
          background: red;
          border-radius: 5px;
          padding: 8px 15px;
          font-size: 20px;
          display: inline-block;
          transition: 0.1s linear;
          text-align: end;

          &:hover {
            transition: 0.1s linear;
            transform: scale(1.1);
          }
        }
      }
    }
  }

  .specialInfo {
    display: none;
  }
}

@media (max-width: 1200px) {
  .productCardWrapper {
    width: 30%;

    .productCard {
      border: 1px solid rgba(255, 255, 255, 0.3);

      .cardContent {
        padding: 20px;
        font-size: 20px;

        .name {
          height: 90px;
          font-size: 16px;
          font-family: 'Montserrat', sans-serif;
          font-weight: 500;
          line-height: 17px;
        }

      }
    }
  }
}

