.navMenu {
  ul {
    li {
      margin-right: 40px;
      height: 40px;
      display: flex;
      align-items: center;
      position: relative;

      .basketQuantity {
        position: absolute;
        top: -12px;
        right: -22px;
        background: darkgreen;
        border: 1px solid #fff;
        height: 20px;
        width: 20px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        padding: 12px;
      }

      a {
        overflow: hidden;

        &:after {
          content: '';
          display: block;
          width: 100%;
          height: 2px;
          background: #f6dd3d;
          transform: translateX(-101%);
          transition: 0.2s ease;
          margin-top: 2px;
        }

        &.active {
          color: #f6dd3d;

          &:after {
            transform: translateX(0);
          }
        }
      }
    }
  }
}

@media (max-width: 991px) {
  .navMenu {
    ul {
      flex-direction: column;
      align-items: center;
      text-align: center;

      li {
        margin: 0 0 40px 0;
        a {
          &:last-child {
            margin-bottom: 20px;
          }
        }
      }
    }
  }
}

@media (max-width: 480px) {
  .navMenu {
    ul {
      li {
        margin: 0 0 70px 0;
        a {
          &:last-child {
          }
        }
      }
    }
  }
}
