.footerWrapper {
  background: rgba(0, 0, 0, 0.6);
  margin-top: 30px;

  .footer {
    padding: 45px 15px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    max-width: 900px;
  }
}

@media (max-width: 480px) {
  .footerWrapper {
    .footer {
      display: flex;
      flex-direction: column;
      justify-content: center;
      font-size: 20px;

      img {
        width: 30px;
      }
    }
  }
}